<template>
  <div class="container">
    <div class="mt-4">
      <h1 class="text-center"></h1>
      <div class="text-end mt-4">
        <button @click="openModal('new')" class="btn btn-primary">
          
        </button>
      </div>
      <table class="table mt-4">
        <thead>
          <tr>
            <th width="6%"></th>
            <th width="10%"></th>
            <th width="10%"></th>
            <th width="20%"></th>
            <th width="12%"></th>
            <th width="10%"></th>
            <th width="10%"></th>
            <th width="10%"></th>
            <th width="12%"></th>
          </tr>
        </thead>
        <!--  v-for  :key -->
        <tbody id="productList" v-for="item in products" :key="item">
          <tr class="align-middle">
            <td>{{ item.num }}</td>
            <td>
              <img class="product-img" :src="item.imageUrl" :alt="item.title" />
            </td>
            <td>{{ item.title }}</td>
            <td>{{ item.description }}</td>
            <td>{{ item.content }}</td>
            <td>{{ item.origin_price }}</td>
            <td>{{ item.price }}</td>
            <td>
              <span v-if="item.is_enabled" class="text-success"></span>
              <span v-else></span>
            </td>
            <td>
              <div class="btn-group">
                <button
                  type="button"
                  class="btn btn-outline-primary btn-sm"
                  @click="openModal('edit', item)"
                >
                  
                </button>
                <button
                  type="button"
                  class="btn btn-outline-danger btn-sm"
                  @click="openModal('delete', item)"
                >
                  
                </button>
              </div>
            </td>
          </tr>
        </tbody>
      </table>
      <p>
         <span id="productCount">{{ allproductsNum.length }}</span> 
      </p>
      <div class="d-flex justify-content-center mt-5">
        <Pagination :page="pagination" @get-product="getData"></Pagination>
      </div>

      <!-- Modal -->
      <AdminProductModal
        ref="adminControlModal"
        :where-product="tempProduct"
        @update-product="updateProduct"
      ></AdminProductModal>
      <!--  Modal -->
      <DelProductModal
        ref="adminDelModal"
        :temp-product="tempProduct"
        @delete-product="deleteProduct"
      ></DelProductModal>
    </div>
  </div>
</template>

<script>
import Pagination from "@/components/Pagination.vue";
import AdminProductModal from "@/components/AdminProductModal.vue";
import DelProductModal from "@/components/DelProductModal.vue";

export default {
  data() {
    return {
      isNew: true,
      products: [],
      allproductsNum: "",
      tempProduct: {
        imagesUrl: [],
      },
      pagination: {},
    };
  },
  components: {
    Pagination,
    AdminProductModal,
    DelProductModal,
  },
  mounted() {
    this.getData();
  },
  methods: {
    // 
    getData(num = this.pagination.current_page || 1) {
      // 
      const url = `${process.env.VUE_APP_API}api/${process.env.VUE_APP_PATH}/products?page=${num}`;
      this.$http
        .get(url)
        .then((res) => {
          if (res.data.success) {
            const { products, pagination } = res.data;
            this.products = products;
            this.pagination = pagination;
            this.getAllproducts();
          }
        })
        .catch((error) => {
          console.log(error);
        });
    },
    getAllproducts() {
      const url = `${process.env.VUE_APP_API}api/${process.env.VUE_APP_PATH}/products/all`;
      this.$http
        .get(url)
        .then((res) => {
          this.allproductsNum = Object.values(res.data.products); // 
        })
        .catch((error) => {
          console.log(error);
        });
    },
    deleteProduct() {
      const url = `${process.env.VUE_APP_API}api/${process.env.VUE_APP_PATH}/admin/product/${this.tempProduct.id}`;
      this.$http
        .delete(url)
        .then((res) => {
          this.$refs.adminDelModal.closeAdminModal(); //  modal
          alert(`${this.tempProduct.title}`);
          console.log(res.data);
          this.getData();
        })
        .catch((error) => {
          console.log(error);
        });
    }, // modal
    openModal(isNew, item) {
      // console.log(isNew, item)
      switch (isNew) {
        case "new":
          // 
          this.tempProduct = {
            imagesUrl: [],
          };
          this.isNew = true;
          this.$refs.adminControlModal.openAdminModal();
          break;
        case "edit":
          // 
          this.getData();
          this.tempProduct = { ...item };
          this.isNew = false;
          this.$refs.adminControlModal.openAdminModal();
          break;
        case "delete":
          // 
          this.tempProduct = { ...item };
          // Modal title  id
          this.$refs.adminDelModal.openAdminModal();
          break;
        default:
          break;
      }
    },
    // modal
    updateProduct(tempProduct) {
      //  
      let url = `${process.env.VUE_APP_API}api/${process.env.VUE_APP_PATH}/admin/product/`;
      let httpMethod = "post";
      //  isNew  post  put API
      if (!this.isNew) {
        // 
        url = `${process.env.VUE_APP_API}api/${process.env.VUE_APP_PATH}/admin/product/${tempProduct.id}`;
        httpMethod = "put";
      }
      this.$http[httpMethod](url, { data: tempProduct }) // post  put
        .then((res) => {
          if (res.data.success) {
            this.$refs.adminControlModal.closeAdminModal(); //  modal
            this.getData(); // 
          } else {
            alert(res.data.message);
          }
        })
        .catch((error) => {
          console.log(error);
        });
    },
  },
  created() {
    console.clear();
  },
};
</script>
