<template>
  <div
    class="modal fade"
    ref="productModal"
    tabindex="-1"
    aria-labelledby="productModalLabel"
    aria-hidden="true"
  >
    <div class="modal-dialog modal-xl modal-dialog-centered">
      <div class="modal-content border-0">
        <div class="modal-header bg-primary text-white">
          <h5 id="productModalLabel" class="modal-title">
            <span></span>
          </h5>
          <button
            type="button"
            class="btn-close"
            data-bs-dismiss="modal"
            aria-label="Close"
          ></button>
        </div>
        <div class="modal-body">
          <div class="row">
            <div class="col-sm-4">
              <div class="mb-1">
                <div class="form-group">
                  <label
                    class="text-light bg-secondary mb-2 py-1 px-2"
                    for="mainImageUrl"
                    ></label
                  >
                  <input
                    id="mainImageUrl"
                    type="text"
                    class="form-control"
                    placeholder=""
                    v-model.trim="whereProduct.imageUrl"
                  />
                  <img
                    class="img-fluid my-3 form-img"
                    :src="whereProduct.imageUrl"
                    :alt="whereProduct.title"
                  />
                </div>

                <div>
                  <label
                    class="btn btn-outline-success btn-sm d-block w-100 mt-3"
                  >
                    <input
                      id="upload_img"
                      style="display: none"
                      type="file"
                      @change="uploadMainImgage"
                    />
                    
                  </label>
                </div>
              </div>
              <div class="mt-4 mb-1"></div>
              <!--   -->
              <div v-if="Array.isArray(whereProduct.imagesUrl)">
                <div
                  class="mb-4"
                  v-for="(item, index) in whereProduct.imagesUrl"
                  :key="'addImage' + index"
                >
                  <hr />
                  <div class="form-group">
                    <label
                      class="text-light bg-secondary mb-2 py-1 px-2"
                      :for="'subImageUrl-' + index"
                      > {{ index + 1 }} </label
                    >
                    <input
                      :id="'subImageUrl-' + index"
                      type="text"
                      class="form-control"
                      placeholder=""
                      v-model="whereProduct.imagesUrl[index]"
                    />
                    <img class="img-fluid my-3 form-img" :src="item" alt />

                    <div>
                      <label
                        class="
                          btn btn-outline-success btn-sm
                          d-block
                          w-100
                          my-3
                        "
                      >
                        <input
                          id="upload_img"
                          style="display: none"
                          type="file"
                          @change="uploadSubImgage"
                        />
                        
                      </label>
                    </div>

                    <div class="mb2">
                      <button
                        class="btn btn-outline-danger btn-sm d-block w-100"
                        @click="whereProduct.imagesUrl.splice(index, 1)"
                      >
                        
                      </button>
                    </div>
                  </div>
                </div>
                <div
                  class="mt-4"
                  v-if="
                    !whereProduct.imagesUrl.length ||
                    whereProduct.imagesUrl[whereProduct.imagesUrl.length - 1]
                  "
                >
                  <button
                    class="btn btn-outline-primary btn-sm d-block w-100"
                    @click="whereProduct.imagesUrl.push('')"
                  >
                    
                  </button>
                </div>
              </div>
              <div v-else>
                <div class="mt-4 mb-2">
                  <button
                    class="btn btn-outline-primary btn-sm d-block w-100"
                    @click="createImages"
                  >
                    
                  </button>
                </div>
              </div>
            </div>
            <div class="col-sm-8">
              <div class="form-group">
                <label for="title"></label>
                <input
                  id="title"
                  type="text"
                  class="form-control mt-1"
                  placeholder=""
                  v-model.trim="whereProduct.title"
                />
              </div>

              <div class="row mt-3">
                <div class="form-group col-md-6">
                  <label for="category"></label>
                  <input
                    id="category"
                    type="text"
                    class="form-control mt-1"
                    placeholder=""
                    v-model="whereProduct.category"
                  />
                </div>
                <div class="form-group col-md-6">
                  <label for="unit">(...)</label>
                  <input
                    id="unit"
                    type="text"
                    class="form-control mt-1"
                    placeholder=""
                    v-model="whereProduct.unit"
                  />
                </div>
              </div>

              <div class="row mt-3">
                <div class="form-group col-md-6">
                  <label for="origin_price"></label>
                  <input
                    id="origin_price"
                    type="number"
                    min="0"
                    class="form-control mt-1"
                    placeholder=""
                    v-model.number="whereProduct.origin_price"
                  />
                </div>
                <div class="form-group col-md-6">
                  <label for="price"></label>
                  <input
                    id="price"
                    type="number"
                    min="0"
                    class="form-control mt-1"
                    placeholder=""
                    v-model.number="whereProduct.price"
                  />
                </div>
              </div>
              <hr />

              <div class="form-group">
                <label for="description"></label>
                <textarea
                  id="description"
                  type="text"
                  class="form-control mt-1"
                  placeholder=""
                  rows="6"
                  v-model.trim="whereProduct.description"
                >
                </textarea>
              </div>
              <div class="form-group mt-3">
                <label for="content"></label>
                <textarea
                  id="content"
                  type="text"
                  class="form-control mt-1"
                  placeholder=""
                  rows="4"
                  v-model.trim="whereProduct.content"
                >
                </textarea>
              </div>
              <div class="form-group mt-3">
                <div class="form-check">
                  <input
                    id="is_enabled"
                    class="form-check-input"
                    type="checkbox"
                    :true-value="1"
                    :false-value="0"
                    v-model="whereProduct.is_enabled"
                  />
                  <label class="form-check-label" for="is_enabled"
                    ></label
                  >
                </div>
              </div>
            </div>
          </div>
        </div>
        <div class="modal-footer">
          <button
            type="button"
            class="btn btn-outline-secondary"
            data-bs-dismiss="modal"
          >
            
          </button>
          <button
            type="button"
            class="btn btn-primary"
            @click="$emit('update-product', whereProduct)"
          >
            
          </button>
        </div>
      </div>
    </div>
  </div>
</template>

<script>
import Modal from "bootstrap/js/dist/modal";
export default {
  props: {
    whereProduct: {
      type: Object,
      default() {
        return {};
      },
    },
  },
  methods: {
    // 
    createImages() {
      this.whereProduct.imagesUrl = [""];
    },
    uploadMainImgage(e) {
      console.dir(e);
      const url = `${process.env.VUE_APP_API}api/${process.env.VUE_APP_PATH}/admin/upload`;
      const file = e.target.files[0];
      const formData = new FormData();
      formData.append("file-to-upload", file);
      this.$http
        .post(url, formData)
        .then((res) => {
          console.log(res);
          this.whereProduct.imageUrl = res.data.imageUrl;
        })
        .catch((err) => {
          console.log(err);
        });
    },
    uploadSubImgage(e) {
      console.dir(e);
      const url = `${process.env.VUE_APP_API}api/${process.env.VUE_APP_PATH}/admin/upload`;
      const file = e.target.files[0];
      const formData = new FormData();
      formData.append("file-to-upload", file);
      this.$http
        .post(url, formData)
        .then((res) => {
          console.log(res);
          this.whereProduct.imagesUrl[this.whereProduct.imagesUrl.length - 1] =
            res.data.imageUrl;
        })
        .catch((err) => {
          console.log(err);
        });
    },
    openAdminModal() {
      this.modal.show();
    },
    closeAdminModal() {
      this.modal.hide();
    },
  },
  data() {
    return {
      modal: "",
    };
  },
  mounted() {
    this.modal = new Modal(this.$refs.productModal, {
      keyboard: false,
    });
  },
};
</script>
